//
// topbar.scss
//

// Logo
.logo {
  display: block;

  span.logo-lg {
    display: block;
  }

  span.logo-sm {
    display: none;
  }

  .logo-lg-text-dark {
    color: $dark;
    font-weight: $font-weight-bold;
    font-size: 22px;
    text-transform: uppercase;
  }

  .logo-lg-text-light {
    color: $white;
    font-weight: $font-weight-bold;
    font-size: 22px;
    text-transform: uppercase;
  }

  svg {
    transition: 0.25s;
  }

  &:hover {
    svg {
      transform: translateY(-5px);
    }
  }
}

.logo-box {
  height: $topbar-height;
  width: $leftbar-width;
  float: left;
  transition: all 0.1s ease-out;

  @media (max-width: 767.98px) {
    height: $topbar-height-mobile;
  }

  .logo {
    line-height: $topbar-height;
    text-align: center;
  }
}

.logo-light {
  display: $logo-light-display;
}

.logo-dark {
  display: $logo-dark-display;
}

.navbar-custom {
  background-color: #fff;
  box-shadow: $shadow;
  position: fixed;
  left: 0;
  right: 0;
  height: $topbar-height;
  z-index: 1003;

  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .actions-visible & {
      &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(218, 232, 247, 0.2);
        z-index: 5;
      }
    }
  }

  @media (max-width: 767.98px) {
    height: $topbar-height-mobile;
    z-index: 1002;
  }

  .topnav-menu {
    > li {
      float: left;
    }

    .nav-link {
      position: relative;
      padding: 0 10px;
      color: rgba($white, 0.6);
      min-width: 32px;
      display: block;
      line-height: $topbar-height;
      text-align: center;
      max-height: $topbar-height;

      @media (max-width: 767.98px) {
        line-height: $topbar-height-mobile;
        max-height: $topbar-height-mobile;
      }
    }
  }

  .dropdown.show {
    .nav-link {
      background-color: rgba($white, 0.05);
    }
  }

  .container-fluid {
    padding: 0;

    &._editor-top {
      @media (max-width: 768px) {
        padding: 20px 0px;
      }
    }
  }

  /* Search */
  .app-search {
    height: $topbar-height;
    display: table;
    width: auto;
    margin-right: 26px;

    @media (max-width: 767.98px) {
      height: $topbar-height-mobile;
    }

    .app-search-box {
      display: table-cell;
      vertical-align: middle;
      position: relative;

      input::placeholder {
        font-size: 0.8125rem;
        color: #758796;
        opacity: 0;
        visibility: hidden;
        transition: 0.35s;
      }
    }

    .form-control {
      border: 1px solid #f6f9fc;
      height: 34px;
      padding-left: 20px;
      padding-right: 0;
      background-color: #f6f9fc;
      box-shadow: none;
      border-radius: 30px 0 0 30px;
      width: 72px;
      transition: 0.35s;
      border-right: none;
      font-weight: 500;
      color: #212b35;

      &:hover,
      &:focus {
        width: 210px;
        border-color: #f6f9fc !important;

        &::placeholder {
          color: #758796;
          opacity: 1;
          visibility: visible;
          font-weight: 400;
        }
      }

      &:focus {
        &,
        & + .input-group-append .btn {
          border-color: #0000ff !important;
          background: #fff !important;
        }
      }
    }

    .input-group-append {
      margin-left: 0;
      z-index: 4;
    }

    .btn {
      padding: 2px 14.4px;
      background-color: #f6f9fc;
      border-color: transparent;
      color: rgba($white, 0.3);
      border-radius: 0 30px 30px 0;
      box-shadow: none !important;
      border-left: none;
      transition: 0.35s;

      i {
        position: relative;
        top: -1px;
      }
    }
  }

  .button-menu-mobile {
    border: none;
    color: $white;
    display: inline-block;
    height: $topbar-height;
    line-height: $topbar-height;
    width: 60px;
    background-color: transparent;
    font-size: 24px;
    cursor: pointer;

    @media (max-width: 767.98px) {
      height: $topbar-height-mobile;
    }

    @media (min-width: 1029px) {
      display: none;
    }
  }

  .button-menu-mobile.disable-btn {
    display: none;
  }
}

/* Notification */
.noti-scroll {
  max-height: 230px;
}

.notification-list {
  margin-left: 0;

  .noti-title {
    background-color: transparent;
    padding: 15px 20px;
  }

  .noti-icon-badge {
    display: inline-block;
    position: absolute;
    top: 16px;
    right: 10px;
  }

  .notify-item {
    padding: 12px 20px;

    .notify-icon {
      float: left;
      height: 36px;
      width: 36px;
      font-size: 18px;
      line-height: 36px;
      text-align: center;
      margin-right: 10px;
      border-radius: 50%;
      color: $white;
    }

    .notify-details {
      margin-bottom: 5px;
      overflow: hidden;
      margin-left: 45px;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: $gray-800;

      b {
        font-weight: 500;
      }

      small {
        display: block;
      }

      span {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 13px;
      }
    }

    .user-msg {
      margin-left: 45px;
      white-space: normal;
      line-height: 16px;
    }
  }

  .profile-dropdown {
    .notify-item {
      padding: 7px 20px;
    }
  }
}

.noti-icon {
  font-size: 21px;
  vertical-align: middle;
}

.profile-dropdown {
  min-width: 170px;

  i {
    vertical-align: middle;
    margin-right: 5px;
  }
}

.nav-user {
  padding: 0 12px !important;

  img {
    height: 32px;
    width: 32px;
  }
}

// Full Screen
.fullscreen-enable {
  [data-toggle='fullscreen'] {
    .fe-maximize::before {
      content: '\e88d';
    }
  }
}

@include media-breakpoint-down(md) {
  .logo-box {
    width: $leftbar-width-condensed !important;
    padding-right: 0 !important;

    .logo-lg {
      display: none !important;
    }

    .logo-sm {
      display: block !important;
    }
  }
}

@media (max-width: 600px) {
  .navbar-custom {
    .dropdown {
      position: static;

      .dropdown-menu {
        left: 10px !important;
        right: 10px !important;
      }
    }
  }
}

// Light Topbar

body[data-topbar-color='light'] {
  .navbar-custom {
    background-color: $bg-topbar-light !important;
    box-shadow: $box-shadow-sm;

    .topnav-menu {
      .nav-link {
        color: $gray-700;
      }
    }

    /*.dropdown.show {
            .nav-link {
                background-color: rgba($dark, 0.03);
            }
        }*/

    .button-menu-mobile {
      color: $dark;
    }

    /* Search */
    .app-search {
      input::placeholder {
        color: $gray-500 !important;
      }

      .form-control {
        color: $dark;
        background-color: $gray-100;
        border-color: $gray-100;
      }

      .btn {
        background-color: $gray-100;
        color: $gray-400;
      }
    }
  }

  .logo-dark {
    display: block;
  }

  .logo-light {
    display: none;
  }
}

@include media-breakpoint-down(md) {
  body[data-layout-mode='horizontal'] {
    // Navbar Button - Horizontal
    .navbar-toggle {
      border: 0;
      position: relative;
      padding: 0;
      margin: 0;
      cursor: pointer;

      .lines {
        width: 25px;
        display: block;
        position: relative;
        height: 16px;
        transition: all 0.5s ease;
        margin-top: calc(#{$topbar-height - 16px} / 2);

        @media (max-width: 767.98px) {
          margin-top: calc(#{$topbar-height-mobile - 16px} / 2);
        }
      }

      span {
        height: 2px;
        width: 100%;
        background-color: $gray-700;
        display: block;
        margin-bottom: 5px;
        transition: transform 0.5s ease;
      }

      &.open {
        span {
          position: absolute;

          &:first-child {
            top: 7px;
            transform: rotate(45deg);
          }

          &:nth-child(2) {
            visibility: hidden;
          }

          &:last-child {
            width: 100%;
            top: 7px;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}

body[data-layout-mode='horizontal'] {
  .button-menu-mobile {
    display: none;
  }

  .logo-box {
    width: auto;
    padding-right: 50px;
    background-color: transparent;
  }

  @media (min-width: 992px) and (max-width: 1366px) {
    .logo-box {
      padding-left: 20px;
    }
  }
}

@media (max-width: 360px) {
  .navbar-custom {
    .topnav-menu {
      .nav-link {
        padding: 0 12px;
      }
    }

    .button-menu-mobile {
      width: 45px;
    }
  }
}
