//
// custom-radio.scss
//

.radio {
  label {
    display: inline-block;
    padding-left: 6px;
    position: relative;
    font-weight: 500;
    font-size: 12px;
    color: #212b35;
    margin-bottom: 0;
    transition: 0.25s;
    cursor: pointer;
    &::before {
      background-color: transparent;
      border-radius: 50%;
      border: 1px solid #454f5b;
      content: '';
      display: inline-block;
      height: 12px;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      margin-left: -12px;
      position: absolute;
      transition: border 0.25s;
      width: 12px;
      outline: none !important;
    }
    &::after {
      background-color: #0000ff;
      border-radius: 50%;
      content: ' ';
      display: inline-block;
      height: 6px;
      left: 11px;
      margin-left: -20px;
      position: absolute;
      top: 50%;
      transform: scale(0, 0) translateY(-50%);
      transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      width: 6px;
    }
  }
  input[type='radio'] {
    cursor: pointer;
    opacity: 0;
    z-index: 1;
    outline: none !important;
    &:disabled + label {
      opacity: 0.65;
    }

    &.input-validation-error + label {
      &:before {
        border-color: #ed1c24;
      }
    }

    &:hover + label {
      &:before {
        border-color: #0000ff;
      }
    }
  }
  input[type='radio']:focus + label {
    &::before {
      outline-offset: -2px;
      outline: 5px auto -webkit-focus-ring-color;
      outline: thin dotted;
    }
  }
  input[type='radio']:checked + label {
    color: #0000ff;

    &::after {
      transform: scale(1, 1) translateY(-50%);
    }

    &::before {
      border-color: #00eba8;
    }
  }
  input[type='radio']:disabled + label {
    &::before {
      cursor: not-allowed;
    }
  }

  &.radio-inline {
    margin-top: 0;
  }

  &.radio-single {
    label {
      height: 17px;
    }
  }
}

@each $color, $value in $theme-colors {
  .radio-#{$color} {
    input[type='radio'] + label {
      &::after {
        background-color: $value;
      }
    }
    input[type='radio']:checked + label {
      &::before {
        border-color: $value;
      }
      &::after {
        background-color: $value;
      }
    }
  }
}
