//
// Custom-radio.scss
//

.checkbox {
  label {
    display: inline-block;
    padding-left: 6px;
    position: relative;
    font-size: 12px;
    font-weight: $label-font-weight;
    margin-bottom: 0;
    vertical-align: middle;
    cursor: pointer;
    &::before {
      background-color: transparent;
      border: 1px solid #454f5b;
      content: '';
      display: inline-block;
      height: 12px;
      left: 0;
      margin: -1px 0 0 -16px;
      position: absolute;
      width: 12px;
      outline: none !important;
      top: 50%;
      transform: translateY(-50%);
      transition: border-color 0.25s;
    }
    &::after {
      color: $gray-700;
      display: inline-block;
      font-size: 11px;
      height: 18px;
      left: 0;
      margin: -1px 0 0 -16px;
      padding-left: 3px;
      padding-top: 2px;
      position: absolute;
      top: 0;
      width: 18px;
    }
  }
  input[type='checkbox'] {
    cursor: pointer;
    opacity: 0;
    z-index: 1;
    outline: none !important;
    &:disabled + label {
      opacity: 0.65;
    }
  }
  input[type='checkbox']:focus + label {
    &::before {
      outline-offset: -2px;
      outline: none;
    }
  }
  input[type='checkbox']:checked + label {
    &::after {
      display: block;
      background: #0000ff;
      content: '';
      position: absolute;
      left: 3px;
      width: 6px;
      height: 6px;
      border-top-width: 0;
      border-left-width: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    &::before {
      border-color: #00eba8;
    }
  }
  input[type='checkbox']:disabled + label {
    &::before {
      background-color: $light;
      cursor: not-allowed;
    }
  }

  &.checkbox-circle {
    label {
      &::before {
        border-radius: 50%;
      }
    }
  }

  &.checkbox-inline {
    margin-top: 0;
  }

  &:not(.checkbox-single) {
    position: relative;
    padding-left: 16px;

    input[type='checkbox'] {
      position: absolute;
      left: 0;
      top: 4px;
    }

    label {
      padding-left: 4px;
    }
  }

  &.checkbox-single {
    position: relative;

    input {
      height: 18px;
      width: 18px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    label {
      height: 12px;
      width: 12px;
      padding-left: 0;
      &:before {
        margin-left: 0;
      }
      &:after {
        margin-left: 0;
      }
    }
  }

  &:hover {
    label:before {
      border-color: #0000ff;
    }
  }
}

@each $color, $value in $theme-colors {
  .checkbox-#{$color} {
    input[type='checkbox']:checked + label {
      &::before {
        background-color: $value;
        border-color: $value;
      }
      &::after {
        border-color: $white;
      }
    }
  }
}
