@media (max-width: 1124px) {
  .navbar-search {
    display: none;
  }
}
@media (max-width: 1028px) {
  .breadcrumbs {
    display: none;
  }
}
@media (max-width: 767.98px) {
  .nav-bordered a.active {
    border-width: 2px !important;
  }

  .cust-card-wrap .btn {
    margin-top: 10px;
  }

  .button_lg {
    font-size: 14px;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
  }

  .contacts-card__number {
    font-size: 30px;
  }

  .nav-tabs .nav-link,
  .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link.active {
    font-size: 13px;
  }

  .statistics-card__inner {
    width: 100%;
  }

  .cust-card-wrap__buttons_aside {
    margin-top: 20px;
  }

  .logo-box {
    display: none;
  }
}
@media (max-width: 576px) {
  .nav-bordered {
    .nav-item {
      margin-right: 14px;
    }

    li a {
      padding: 6px 4px 4px !important;
    }
  }

  .tab-content {
    padding-top: 12px;
  }

  .nav-tabs .nav-link,
  .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link.active {
    font-size: 12px;
  }

  .statistics-card__knobs {
    flex-direction: column;
    margin-top: 16px;
  }

  .statistics-card__knobs-item {
    & + & {
      margin-top: 18px;
    }
  }

  .statistics-card__inner {
    width: auto;
  }
}
@media (max-width: 320px) {
  .nav-tabs .nav-link,
  .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link.active {
    font-size: 11px;
  }
}
