// Global
html,
body {
  display: flex;
  flex-direction: column;
}

body {
  flex: 1;
  margin: 0;

  &:after {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: transparent;
    pointer-events: none;
    z-index: 1001;
  }

  &.nav-is-toggled {
    &:after {
      background: rgba(#0000ff, 0.2);

      @media (min-width: 768px) {
        display: none;
      }
    }
  }

  &._overlayed {
    pointer-events: none;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: #dae8f7;
      z-index: 1003;
    }
  }

  &._step {
    background: #dae8f7;
  }

  &._off-button-hovered {
    &:after {
      z-index: 1099;
      background: rgba(218, 232, 247, 0.7);
    }
  }
}

#wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.animation-stopper * {
  animation: none !important;
  transition: none !important;
}

.actions-visible .content-page {
  &:after {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#dae8f7, 0.2);
    z-index: 2;
  }
}

.content-page {
  &._editor {
    @media (max-width: 991px) {
      margin-top: 100px;
    }

    @media (max-width: 768px) {
      margin-top: 190px;
    }
  }

  &._no-sidebar {
    margin-left: 0 !important;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 26px;
    margin-top: 80px;
    background-color: #fff;

    @media (min-width: 768px) {
      .content {
        margin-top: -40px;
      }
    }

    @media (max-width: 767px) {
      margin-top: 60px;
    }
  }

  &._set-password {
    background-color: #b6ffe8;

    .content {
      width: 750px;
      align-self: center;
      padding: 40px;
      background-color: #fff;
      position: relative;

      @media (max-width: 767px) {
        width: 100%;
        flex: 1;
        margin-top: 0;
        display: flex;
        flex-direction: column;
        padding: 20px;
      }
    }

    .container-fluid {
      //width: 384px;

      .login__form {
        margin: 0 auto;
        width: 384px;

        @media (max-width: 767px) {
          width: auto;
        }
      }

      @media (max-width: 767px) {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: auto;
        padding: 0;
        margin: 0;
      }
    }

    @media (max-width: 767px) {
      padding: 0;

      p br {
        display: none;
      }
    }
  }

  &._ready-to-go {
    align-items: center;
    padding-top: 118px;
    padding-bottom: 0;
    justify-content: flex-start;
  }

  &._modal {
    margin-top: 64px;
    margin-left: 0 !important;
  }

  &._settings {
    margin-top: 64px;
    margin-left: 0 !important;
    padding-left: 78px;
    padding-right: 78px;
    padding-bottom: 53px;

    &._emailFooter {
      align-items: center;
      justify-content: center;
      min-height: 100vh;
      height: 100%;
      margin-top: 0px;
      display: flex;
    }

    @media (max-width: 576px) {
      padding-left: 12px;
      padding-right: 12px;
    }

    @media (max-width: 398px) {
      .create-campaign__row-content {
        margin-right: 24px;
      }
    }
  }
}

.navbar-custom {
  &._login {
    background-color: #0000ff;
    box-shadow: none;

    .container-fluid {
      padding: 0 40px;
      height: 100%;

      @media (max-width: 767px) {
        padding: 0 20px;
      }
    }
  }
}

// Btn
.btn {
  display: inline-flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  border-radius: 0;
  transition: 0.25s;
  color: #000;
  white-space: nowrap;

  &:hover {
    color: #fff;
  }

  &:focus {
    box-shadow: none !important;
  }

  &_icon {
    border: none;
    outline: none;
    background: none;
    width: auto;
    height: auto;
    padding: 0;

    .add-to-fav-icon__bg,
    .add-to-fav-icon__plus {
      transition: 0.25s;
    }

    &:hover,
    &:focus {
      .add-to-fav-icon__bg {
        fill: #0000ff;
      }

      .add-to-fav-icon__plus {
        stroke: #fff;
        transform: translate(-1980px, -1089.5px);
      }
    }

    &._arrow {
      padding: 6px;
      color: #00eba8;
      transition: 0.25s;

      &:hover {
        color: #0000ff;
      }
    }

    &._fav {
      transition: 0.25s;

      .fav-body {
        transition: 0.25s;
      }

      &:hover {
        transform: scale(1.1);

        .fav-body {
          fill: #fff;
        }

        .fav-border {
          fill: blue;
        }
      }

      &.active {
        .fav-body {
          fill: blue;
        }
      }
    }

    &._arrow-right,
    &._arrow-left {
      color: #b6ffe8;
      transition: 0.25s;

      &:hover {
        color: #0000ff;
      }
    }

    &._arrow-right {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &_back {
    width: 20px;
    height: 20px;
    position: fixed;
    transition: 0.25s;

    @media (min-width: 768px) {
      display: flex;
      top: 90px;
      right: 10px;
      z-index: 1;
      border-radius: 50%;
      justify-content: center;
      align-items: center;
      color: #000;
      background: #b6ffe8;

      &:hover {
        color: #fff;
        background: #0000ff;
      }
    }

    @media (max-width: 767.98px) {
      display: block;
      top: 64px;
      right: 20px;
      width: 16px;
      height: 16px;
      z-index: 1002;
      background: transparent;
      border: 1px solid #00eba8;
      border-radius: 50%;
      padding: 1px;
      background-color: #00eba8;
      color: #fff;

      &:hover {
        color: #00eba8;
      }

      svg {
        width: 100%;
        height: 100%;
        display: block;
      }
    }

    @media (max-width: 576px) {
      top: 22px;
    }
  }

  &._tag-del {
    background: #fff;
    border-color: #00eba8;
    color: #000;
    cursor: default !important;
    margin: 16px 20px 0px 0px;
    padding-right: 5px;
    white-space: nowrap;

    .btn_icon {
      position: relative;
      height: 8px;
      width: 8px;
      transition: 0.25s;

      svg {
        display: block;
        width: 100%;
      }

      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 20px;
        height: 20px;
        transform: translate(-50%, -50%);
      }
    }

    &:hover,
    &:focus {
      border-color: #b6ffe8;
      color: #000;

      .btn_icon {
        transform: scale(1.25);
      }
    }
  }

  &._tag {
    background: #fff;
    border-color: #00eba8;
    color: #000;
    margin: 16px 20px 0px 0px;
    white-space: nowrap;

    &:hover,
    &:focus {
      background: #0000ff;
      border-color: #0000ff;
      color: #fff;
    }
  }

  // Size
  &._sm {
    font-size: 12px;
    font-weight: 500;
    padding: 0 8px;
    height: 25px;
    line-height: 25px;
    width: fit-content;
  }

  &._lg {
    font-size: 16px;
    padding: 0 12px;
    height: 34px;
    line-height: 34px;
    font-weight: 500;

    @media (max-width: 576px) {
      &._sm {
        font-size: 12px;
        font-weight: 500;
        padding: 0 8px;
        height: 25px;
        line-height: 25px;
        width: fit-content;
      }
    }

    .loader {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0.85);
      width: 78px;
      height: 22px;
    }
  }

  &._xl {
    font-size: 18px;
    height: 45px;
    padding: 0 20.5px;

    &:hover {
      transform: scale(0.925);
    }
  }

  &._wide {
    min-width: calc(20% - 24px);
  }

  &._long {
    min-width: 125px;
  }

  // Colors
  &._primary {
    &,
    &:focus {
      background: #00eba8;
      border-color: #00eba8;
      color: #000;
    }
  }

  &._secondary {
    &,
    &:focus {
      background: #b6ffe8;
      border-color: #b6ffe8;
      color: #000;
    }
  }

  &._primary,
  &._secondary {
    &:hover {
      background: #0000ff;
      border-color: #0000ff;
      color: #fff;
    }
  }

  &._round {
    border-radius: 30px;
  }

  &._check {
    padding: 0;
    width: 25px;
    border: 0px !important;
    line-height: 1 !important;
    font-size: 0px !important;

    &:hover {
      border: 0px !important;
    }

    & + & {
      margin-left: 10px;
    }
  }

  &._sync-list {
    width: 14px;
    height: 14px;
    padding: 0;
    border-radius: 50%;
    position: relative;

    svg {
      margin: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
    }

    & + ._sync-list {
      margin-left: 7px;
    }
  }

  &._social-integration {
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    align-items: center;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background: #b6ffe8;
    overflow: hidden;
    cursor: default !important;

    i {
      color: #00eba8;
    }

    ._connected & {
      background: #0000ff;

      i {
        color: #fff;
      }
    }
  }

  &._loading {
    background: #0000ff;
    border-color: #0000ff;
    position: relative;
    color: transparent !important;
    pointer-events: none;
    user-select: none;

    .loader {
      display: block;
    }

    & + .dropdown._actions {
      display: none;
    }
  }

  .loader {
    display: none;
    transform: scale(0.615);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .loader-dot {
    position: absolute;
    top: 5px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);

    &:nth-child(1) {
      left: 8px;
      animation: lds-ellipsis1 0.6s infinite;
    }

    &:nth-child(2) {
      left: 8px;
      animation: lds-ellipsis2 0.6s infinite;
    }

    &:nth-child(3) {
      left: 32px;
      animation: lds-ellipsis2 0.6s infinite;
    }

    &:nth-child(4) {
      left: 56px;
      animation: lds-ellipsis3 0.6s infinite;
    }
  }

  &._login {
    border: 1px solid #00eba8;
    padding: 0;
    margin: 0 10px;
    width: 32px;
    height: 32px;

    svg {
      transition: 0.15s ease-in-out;
    }

    &:hover {
      border-color: #0000ff;
      background-color: #0000ff;

      svg {
        transform: scale(0.8);
      }

      path {
        fill: #fff;
      }
    }
  }
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(24px, 0);
  }
}

// Link
.link {
  color: #212b35;
  font-weight: 700;
  display: inline-block;
  line-height: 1;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  width: fit-content;

  @media (max-width: 768px) {
    white-space: pre-wrap;
  }

  &:hover {
    color: #212b35;

    &:after {
      height: 14px;
    }
  }

  &:after {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    background: #b6ffe8;
    z-index: -1;
    transition: 0.25s;
  }

  h4 & {
    &:hover {
      &:after {
        height: 17px;
      }
    }
  }

  &._lg {
    font-size: 36px;
    font-weight: 400;
    line-height: 43.2px;

    &:after {
      height: 5px;
      bottom: 8px;
    }

    &:hover {
      &:after {
        height: 31px;
      }
    }
  }

  &.font-12 {
    &:after {
      bottom: 1px;
    }

    &:hover {
      &:after {
        height: 13px;
      }
    }
  }
}

// Dropdown
.dropdown {
  &._more {
    position: absolute;
    right: 0;
    top: 0;

    /*&.show {
      &:after {
        content: '';
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(#DAE8F7, 0.2);
      }
    }*/
  }

  &._user {
    z-index: 4;
  }

  &._more.show,
  &._actions.show {
    [data-toggle='dropdown'] {
      background: #00f;
      border-color: #00f;
      color: #fff;
    }
  }

  &-menu {
    &._actions {
      border-radius: 0;
      border: 1px solid #0000ff;
      box-shadow: none;
      min-width: 128px;
      box-sizing: border-box;
      padding: 0;
      animation: none;
      transform: none !important;

      &.show[x-placement='top-end'] {
        transform: translateY(-100%) !important;
        top: -13px !important;
      }

      &._sm {
        min-width: 113px;
      }

      .dropdown-menu-inner {
        position: relative;
        width: 100%;
        height: 100%;
        padding: 14px 0 10px;
        background: #fff;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          transform: translate(4px, 4px);
          display: block;
          width: 100%;
          height: 100%;
          background: #0000ff;
          z-index: -1;
        }
      }

      .dropdown-item {
        font-weight: 500;
        font-size: 12px;
        transition: 0.25s;
        text-align: left;
        padding: 6px 27px;

        &:hover {
          background: #f6f9fc;
          color: #0000ff;
        }
      }
    }
  }

  &__divider {
    height: 1px;
    background: #00eba8;
    margin: 11px 27px 10px;
  }
}

.dropdown-toggle {
  display: inline-block;
  font-weight: 500;
  padding: 10px;
  transition: color 0.25s, background 0.25s, border 0.25s, transform 0.25s;

  &._outline {
    border: 1px solid #0000ff;
    border-radius: 23px;
    color: #000000;
    font-size: 18px;

    @media (max-width: 991px) {
      font-size: 14px;
    }

    &:hover {
      background: #0000ff;
      color: #fff;
      transform: scale(0.9);

      svg {
        color: #fff;
      }
    }

    svg {
      color: #0000ff;
      transition: transform 0.25s, color 0.25s;
    }
  }

  &._solid {
    border: 1px solid #00eba8;
    background: #00eba8;
    border-radius: 23px;
    color: #000000;
    font-size: 18px;

    @media (max-width: 991px) {
      font-size: 14px;
    }

    &:hover {
      background: #0000ff;
      border-color: #0000ff;
      color: #fff;

      svg {
        color: #fff;
      }
    }

    svg {
      color: #fff;
      transition: transform 0.25s, color 0.25s;
    }
  }

  &._lg {
    line-height: 44px;
    padding: 0 19px;

    @media (max-width: 991px) {
      line-height: 34px;
      padding: 0 15.5px;
    }

    svg {
      margin-left: 4.6px;
    }
  }

  &._md {
    line-height: 34px;
    padding: 0 15.5px;

    svg {
      margin-left: 4.6px;
    }
  }

  &._icon {
    color: #00eba8;
    position: relative;

    &:hover,
    .show & {
      color: #0000ff;

      svg {
        transform: translateY(-3px);
      }

      .badge {
        transform: translateX(8px);
      }
    }

    .badge {
      background-color: #ed1c24;
      border-radius: 50%;
      transition: 0.25s;
      position: absolute;
      top: 4px;
      right: 5px;
      color: #fff;
    }
  }

  &._user {
    padding: 0 22px 0 0;
    z-index: 1;

    .user-avatar {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      box-sizing: border-box;
      color: #000;
      font-size: 18px;
      margin: 17.5px 0;
      transition: 0.25s;
      position: relative;
      overflow: hidden;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
        border: 1px solid #00f;
        background-color: #fff;
      }
    }

    svg {
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      color: #00eba8;
      fill: transparent;
      transition: color 0.25s, fill 0.25s;
    }

    &:hover {
      span {
        background: #00f;
        color: #fff;
        transform: scale(0.92);
      }

      svg {
        color: #00f;
        fill: #00f;
      }

      svg circle {
        fill: #00f;
      }
    }
  }

  &._more {
    width: 22px;
    height: 40px;
    background: #00eba8;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.25s;
    border: none;
    color: #000;
    padding: 0;

    &:hover {
      color: #fff;
      background: #0000ff;
    }
  }

  &._activity {
    min-width: 172px;
    background: #f6f9fc;
    display: inline-block;
    line-height: 34px;
    border-radius: 17px;
    padding: 0 18px 0 21.5px;
    color: #212b35;
    font-size: 12px;
    font-weight: 500;
    transition: 0.25s;

    &:hover {
      color: #fff;
      background: #0000ff;
    }
  }

  svg {
    display: inline-block;
    transition: transform 0.25s;
  }
}

// Statistics card
.knobs {
  display: flex;
  flex: 1;
  justify-content: space-around;

  @media (min-width: 768px) {
    flex-direction: column;
    justify-content: space-between;
    margin-top: 0;
  }

  &._triple {
    margin-top: 38px;
    padding-bottom: 14px;

    @media (max-width: 767px) {
      width: 100%;
    }

    @media (max-width: 576px) {
      flex-wrap: wrap;
      justify-content: space-between;

      & > div {
        margin-bottom: 20px;
        margin-right: 20px;
      }
    }
  }

  &._double {
    margin-top: 16px;
    align-items: center;

    @media (min-width: 768px) {
      padding: 30px 0;
    }

    @media (max-width: 576px) {
      flex-wrap: wrap;
      justify-content: space-between;

      & > div {
        margin-bottom: 20px;
        margin-right: 20px;
      }
    }

    &:after {
      content: '';
      height: 50px;

      @media (max-width: 767.98px) {
        display: none;
      }

      @media (max-width: 374px) {
        flex-direction: column;

        & > div {
          & + div {
            margin-top: 22px;
          }
        }
      }
    }
  }
}

// List Add Success
.confirmation {
  position: relative;
  margin: 13px 12px 40px 22px;
  z-index: 1002;

  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translate(-10px, 10px);
    z-index: -1;
  }

  &:before {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(#dae8f7, 0.7);
    z-index: -2;
  }

  &._positive {
    & > div {
      border: 1px solid #0000ff;
    }

    &:after {
      background: #0000ff;
    }
  }

  &._negative {
    & > div {
      border: 1px solid #ed1c24;
    }

    &:after {
      background: #ed1c24;
    }
  }

  &._login {
    margin: 10px 0 40px;

    &:before {
      display: none;
    }

    @media (min-width: 768px) {
      width: 400px;
    }
  }

  button.close {
    position: absolute;
    top: 16px;
    right: 14px;
  }
}

button.close {
  background: #b6ffe8;
}

// Contact Details
.card {
  &._contact {
    padding: 0;
    flex-direction: row;
    margin: 14px 0 18px;

    @media (max-width: 767.98px) {
      min-height: 170px;
    }
  }

  &._search {
    background-color: transparent;
    box-shadow: none;

    .plugins-apps__icon {
      fill: #00eba8;
      cursor: pointer;

      &:hover {
        fill: #fff;
        background-color: #00f;
      }
    }

    .btn {
