// Add Contacts
.upload-list {
  &__match-item {
    white-space: nowrap;
    line-height: 1;

    & + & {
      margin-top: 18px;
    }
  }

  &__match-outer {
    position: relative;

    &.match-after {
      &:after {
        display: none;
      }
    }

    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 20px;
      width: 10px;
      background: rgba(#000, 0.2);
    }
  }

  &__match-wrap {
    overflow-x: auto;
    color: #fff;
    transition: color 0.25s;
    overflow-y: hidden;
    max-height: 264px;
    height: 100%;

    &::-webkit-scrollbar-thumb {
      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }

    &::-webkit-scrollbar-track-piece {
      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }

    &::-webkit-scrollbar-track {
      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }

    &:hover {
      color: #b6ffe8;

      &::-webkit-scrollbar-thumb {
        &:vertical {
          background-color: transparent !important;
        }

        background: #fff;
      }
    }

    &:active {
      color: #00f;
      background-color: #fff;
    }

    &::-webkit-scrollbar {
      height: 20px !important;
      background: currentColor;
      transition: 0.5s;

      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }

    &::-webkit-scrollbar-thumb {
      background: #00eba8;
      border: 6px solid currentColor;
      border-radius: 20px;
      transition: 0.5s;

      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }

    &::-webkit-scrollbar-thumb:active {
      background: #0000ff;

      &:vertical {
        background-color: transparent !important;
        width: 10px !important;
        border: 0px !important;
      }
    }
  }
}

.bootstrap-select {
  min-width: 137px;

  .btn {
    font-size: 10px;
    font-weight: 500;
    color: #000 !important;
    background-color: #fff !important;
    border-color: #00eba8 !important;
    padding: 0 4px 0 7px;
    line-height: 20px;
    transition: 0.25s;

    &:not(.bs-placeholder) {
      border-color: #000 !important;
      position: relative;

      &:before {
        content: '';
        display: block;
        width: 13px;
        height: 10px;
        margin-right: 5px;
        background-image: url('../images/icon-check.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }

    &:hover {
      border-color: #0000ff !important;
    }
  }

  .dropdown-toggle {
    &:after {
      content: '';
      width: 6px;
      height: 4px;
      font: 0;
      background-image: url('../images/dropdown-arrow.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      margin-top: 1px;
    }

    &.bs-placeholder {
      &,
      &:active,
      &:focus,
      &:hover {
        color: #000;
      }
    }
  }

  & > .dropdown-menu {
    min-width: 130px;
    right: 0 !important;
    left: auto !important;
    margin-top: 3px;
    padding: 0;
    border-radius: 0;
    border: 1px solid #00f;
    overflow: visible !important;
    box-shadow: 3px 3px 0 0 rgba(0, 0, 255, 1);

    .upload-list__match-wrap & {
      transform: translate3d(0px, 0px, 0px) !important;
      overflow: auto !important;
      max-height: 165px !important;
    }

    & > .inner {
      position: relative;
      z-index: 1;

      .upload-list__match-wrap & {
        overflow: visible !important;
      }

      & > .dropdown-menu {
        padding: 14px 0 10px;
        animation: none !important;
      }
    }
  }

  .dropdown-item {
    padding: 6px 8px;
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    transition: 0.25s;

    &:hover {
      background: #f6f9fc;
      color: #00f;
    }
  }

  &._with-search {
    .btn {
      font-size: 12px;
      padding: 0 9px 0 12px;
      height: 28px;
      border-color: #a0b1ba !important;

      &.bs-placeholder {
        color: #758796 !important;
      }

      &:not(.bs-placeholder) {
        border-color: #212b35 !important;

        &:before {
          display: none;
        }
      }

      &:hover {
        border-color: #0000ff !important;
      }
    }

    &.show {
      .btn {
        border-color: #758796 !important;
        color: #212b35 !important;

        /*&:hover {
          border-color: #0000FF !important;
        }*/
      }
    }

    .dropdown-toggle {
      &:after {
        content: '';
        width: 8px;
        height: 4.94px;
      }
    }

    .dropdown-menu {
      left: 0 !important;
      right: 15px !important;
      max-height: 132px;
      overflow: auto;

      &:hover {
        &::-webkit-scrollbar-thumb {
          background: #0000ff;
        }
      }

      &::-webkit-scrollbar {
        width: 20px;
        background: #fff;
      }

      &::-webkit-scrollbar-thumb {
        background: #00eba8;
        border: 5px solid #fff;
        border-radius: 20px;
      }

      &::-webkit-scrollbar-thumb:active {
        background: #0000ff;
      }
    }

    .dropdown-item {
      text-align: left;
      padding-left: 26px;
      padding-right: 26px;
    }

    .bs-searchbox {
      padding: 17px 5px 10px 25px;
      position: relative;
      z-index: 2;
      background: #fff;

      .form-control {
        border-color: #f6f9fc;
        background: #f6f9fc;
        height: 22px;
        font-size: 10px;
        padding: 0 8px;

        &:hover,
        &:focus {
          background: #fff;
          border-color: #0000ff;
        }
      }
    }

    & > .dropdown-menu {
      padding: 0 23px 0 3px !important;

      & > .inner {
        & > .dropdown-menu {
          padding: 0;
        }
      }
    }
  }
}

// Breadcrumbs
.breadcrumbs {
  display: flex;
}

.breadcrumb-item {
  font-size: 22px;
  font-weight: 500;

  &.active,
  & > a {
    color: #0000ff;
  }

  a {
    margin-right: 6px;
  }
