// Side navigation
.nav {
  &-drill {
    display: flex;
    position: fixed;
    z-index: 1002;
    top: 0;
    left: 0;
    height: 100%;
    background-color: #0000ff;
    -webkit-overflow-scrolling: touch;
    transition: 0.4s;
    transform: translateX(-100%);

    &,
    * {
      box-sizing: border-box;
    }

    .nav-is-toggled & {
      transform: translateX(0);
    }

    @media (max-width: 767.98px) {
      width: calc(100vw - 60px) !important;
      padding-bottom: 100px;

      &__icons {
        position: absolute;
        bottom: 40px;
        left: 40px;
        right: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }

    @media (min-width: 768px) {
      transform: translateX(0);
      width: 268px;
      top: 80px;
      background: #ecf1f8;
      transition: none !important;

      &__icons {
        display: none;
      }
    }

    @media (max-width: 360px) {
      padding-bottom: 65px;

      &__icons {
        bottom: 25px;
        left: 30px;
        right: 30px;

        button {
          height: 30px;
          width: auto;

          svg {
            display: block;
            height: 100%;
            width: auto;
          }
        }
      }
    }
  }

  &-drill > &-items {
    padding: 36px 50px 40px 40px;

    @media (min-width: 768px) {
      width: 100px;
      flex: 0 0 100px;
      padding: 0;
      overflow: auto;
      height: calc(100% - 80px);

      & > .nav-item > .nav-item-link {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 100%;
        height: 66px;
        color: #212b35;
        transition: color 0.25s, background-color 0.25s;
        font-size: 12px;
        font-weight: 600;
        padding: 0.5rem 1rem;
        line-height: 18px;

        &:after {
          display: none;
        }

        svg {
          color: #00eba8;
          transition: color 0.25s, transform 0.25s;
          margin-bottom: 2px;
        }

        &:hover {
          svg {
            color: #00f;
            transform: translateY(-2px);
          }
        }
      }

      & > .nav-item.active > .nav-item-link {
        background-color: #f6f9fc;
        color: #00f;

        svg {
          color: #00f;
        }
      }
    }

    @media (max-width: 360px) {
      padding: 26px 40px 30px 30px;
    }
  }

  &-items {
    flex: 0 0 100%;
    list-style: none;
    padding: 0;
    margin-bottom: 0;

    @media (min-width: 768px) {
      background: #fff;
    }

    @media (max-width: 767.98px) {
      overflow: auto;
    }
  }

  &-item-link {
    display: block;
    padding: 17px 0;
    color: #fff;
    font-size: 26px;
    line-height: 1;
    font-weight: 500;

    &:hover {
      color: #fff;
    }

    &._logo {
      padding: 24px 0;
    }

    @media (max-width: 767.98px) {
      i {
        display: none;
      }
    }

    @media (min-width: 768px) {
      color: #212b35;
      font-size: 14px;
      padding: 0 29px;
      height: 40px;
      line-height: 40px;
      transition: color 0.25s;
      font-weight: 500;

      &:hover {
        color: #00f;
      }

      &.active {
        color: #00f;
        background: #ecf1f8;
      }
    }

    @media (max-width: 360px) {
      font-size: 22px;
      padding: 14px 0;
    }
  }

  &-expand {
    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transform: translateX(-100%);
      background-color: #0000ff;
      transition: 0.4s;
      visibility: hidden;
      opacity: 0;
      padding: 75px 20px 0 40px;

      @media (min-width: 768px) {
        transform: translateX(0);
        width: auto;
        height: calc(100% - 80px);
        overflow: auto;
        left: 100px;
        background-color: #f6f9fc;
        padding: 0;
        transition: opacity 0.25s, visibility 0.25s;

        .nav-item-link {
          min-width: 168px;
          white-space: nowrap;
        }
      }

      @media (max-width: 767.98px) {
        height: calc(100% - 100px);
      }

      @media (max-width: 360px) {
        height: calc(100% - 65px);
      }

      .nav-back {
        position: absolute;
        top: 17px;
        left: 20px;

        @media (min-width: 768px) {
          display: none;
        }
      }

      .nav-title {
        position: absolute;
        top: 14px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 22px;
        color: #fff;
        font-weight: 500;

        @media (min-width: 768px) {
          display: none;
        }
      }
    }

    &-link {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &::after {
        content: '';
        background-image: url('../images/chevron-right.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        flex: 0 1 auto;
        width: 7.41px;
        height: 12px;
      }
    }

    &.active {
      > .nav-expand-content {
        visibility: visible;
        opacity: 1;

        @media (max-width: 767.98px) {
          transform: translateX(0);
        }
      }
    }
  }
}

