// Knobs
.knob-wrap {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;

  &_percent {
    .knob-text {
      position: absolute;
      content: '';
      top: 0px;
      left: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      margin-left: 0px;

      p {
        margin-bottom: 0px;
      }
    }

    &:after {
      display: none;
    }

    &.knob-wrap {
      width: 75px;
      height: 75px;
    }
  }

  &_lg {
    display: block;
    position: relative;
    background: #fff;
    border-radius: 50%;
    width: 75px;
    height: 75px;

    .loader {
      display: none;
      transform: translate(-50%, -50%) scale(0.615);
      position: absolute;
      top: 50%;
      left: 50%;
      width: 77px;
      height: 13px;
    }

    .loader-dot {
      position: absolute;
      top: 0;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background: #0000ff;
      animation-timing-function: cubic-bezier(0, 1, 1, 0);

      &:nth-child(1) {
        left: 8px;
        animation: lds-ellipsis1 0.6s infinite;
      }

      &:nth-child(2) {
        left: 8px;
        animation: lds-ellipsis2 0.6s infinite;
      }

      &:nth-child(3) {
        left: 32px;
        animation: lds-ellipsis2 0.6s infinite;
      }

      &:nth-child(4) {
        left: 56px;
        animation: lds-ellipsis3 0.6s infinite;
      }
    }

    &._loading .loader {
      display: block;
    }
  }

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    background-image: url('../images/knob-arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  &_down:after {
    transform: translate(-50%, -50%) rotate(180deg);
  }

  &__mark {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #0000ff;
    font-size: 24px;
    font-weight: 500;
  }

  &__value {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 14px;
    font-weight: 700;
  }
}

