// Switcher
.switcher {
  height: 0;
  width: 0;
  visibility: hidden;

  & + &__toggle {
    cursor: pointer;
    text-indent: -9999px;
    width: 27px;
    height: 14px;
    background: #dae8f7;
    display: block;
    border-radius: 10px;
    position: relative;
    margin-bottom: 0;
    flex-shrink: 0;

    .setup-payment__main & {
      background-color: #c3d8e5;
    }

    &:after {
      content: '';
      position: absolute;
      top: 2px;
      left: 2px;
      width: 10px;
      height: 10px;
      background: #fff;
      border-radius: 50%;
      transition: 0.3s;
    }

    &:hover {
      &:after {
        background: #0000ff;
      }
    }
  }

  &:checked + &__toggle {
    background: #0000ff;

    &:after {
      background: #fff;
      left: calc(100% - 2px);
      transform: translateX(-100%);
    }
  }

  &__wrap {
    display: flex;
    align-items: center;

    &._auto-sync {
      display: none;
      margin-top: 27px;

      ._chosen + & {
        display: flex;
      }
    }
  }

  &__label {
    margin-left: 8px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 0;
    order: 1;
    user-select: none;

    &:hover {
      & ~ .switcher__toggle {
        &:after {
          background: #0000ff;
        }
      }

      & + .switcher:checked {
        & + .switcher__toggle {
          &:after {
            background: #fff;
          }
        }
      }
    }

    span {
      color: #454f5b;
      font-weight: 500;
    }

    .switcher__wrap._active & {
      color: #0000ff;
    }
  }
}

