    .loader {
      display: none;
      transform: translate(-50%, -50%) scale(0.615);
      position: absolute;
      top: 50%;
      left: 50%;
      width: 77px;
      height: 13px;
    }

    .loader-dot {
      position: absolute;
      top: 0;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background: #0000ff;
      animation-timing-function: cubic-bezier(0, 1, 1, 0);

      &:nth-child(1) {
        left: 8px;
        animation: lds-ellipsis1 0.6s infinite;
      }

      &:nth-child(2) {
        left: 8px;
        animation: lds-ellipsis2 0.6s infinite;
      }

      &:nth-child(3) {
        left: 32px;
        animation: lds-ellipsis2 0.6s infinite;
      }

      &:nth-child(4) {
        left: 56px;
        animation: lds-ellipsis3 0.6s infinite;
      }
    }

    &._loading {
      pointer-events: none;

      .loader {
        display: block;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(#0000ff, 0.5);
      background-image: url('../images/eye.svg');
      background-position: center center;
      background-repeat: no-repeat;
      opacity: 0;
      visibility: hidden;
      transition: 0.15s;
