// Touchspin
.round-touchspin {
  width: 75px;
  height: 75px;
  position: relative;
  border: 1px solid #00eba8;
  border-radius: 50%;
  margin: 9px auto 22.5px;
  background: #fff;
  transition: 0.15s;

  .input-group-append,
  .input-group-prepend {
    position: absolute;
    top: 50%;
  }

  .input-group-prepend {
    left: 0;
    transform: translate(-50%, -50%);
    z-index: 1;
  }

  .input-group-append {
    right: 0;
    transform: translate(50%, -50%);
    transition: 0.15s;
  }

  .input-group {
    position: static;
  }

  .input-group > .form-control {
    border: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0;
    width: 100%;
    height: 100%;
    display: block;
    text-align: center;
    background: transparent;
    font-size: 36px;
    color: #0000ff;
    opacity: 1;
    visibility: visible;
    transition: 0.15s;
    pointer-events: none;
  }

  .btn-primary {
    width: 24px;
    height: 24px;
    border-radius: 50% !important;
    border: none;
    background: #00eba8;
    color: #000;
    padding: 0;
    font-size: 16px;
  }

  .bootstrap-touchspin-down {
    transition: 0.15s;
  }

  &:not(._with-value) {
    border-color: transparent;

    .input-group > .form-control,
    .bootstrap-touchspin-down {
      opacity: 0;
      visibility: hidden;
    }

    .input-group-append {
      right: 50%;
      top: 13px;
      transform: translateX(50%) scale(1.4582);
    }
  }

  &._max {
    .input-group-append {
      z-index: -1;
      opacity: 0.2;
    }
  }

  &._min {
    .input-group-prepend {
      z-index: -1;
      opacity: 0.2;
    }
  }

  &._disabled {
    opacity: 0.2;
    pointer-events: none;
  }
}

