        visibility: hidden;
      }
    }

    .noUi-base {
      opacity: 1;
      visibility: visible;
      transition: opacity 0.15s linear, visibility 0.15s linear;
    }

    &:not(:nth-child(7)) {
      .noUi-value,
      .noUi-marker {
        display: none;
