        .noUi-tooltip {
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    .custom-control-label {
      min-width: 32px;
    }

    .noUi-origin:nth-last-child(2):after {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    .noUi-origin:last-child:after {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    .noUi-target {
      &:before,
      &:after {
        content: '';
        position: absolute;
        display: block;
        width: 16px;
        height: 12px;
        top: 50%;
        background: #b6ffe8;
        pointer-events: none;
        border-radius: 8px;
      }

      &:before {
        left: 0;
        transform: translate(-50%, -50%);
