// Variables
//
// custom-variables
//

//Background left-sidebar
$bg-leftbar: #ffffff;
$bg-leftbar-dark: #38414a;
$bg-leftbar-brand: #4a81d4;
$bg-leftbar-gradient: #683ba9;

// Leftbar width
$leftbar-width: 240px;
$leftbar-width-condensed: 100px;

// Small Leftbar Width
$leftbar-width-sm: 160px;

// Boxed layout width
$boxed-layout-width: 1300px;

// Compact sidebar
$twocolumn-sidebar-width: 216px;
$twocolumn-sidebar-iconview-width: 100px;
$twocolumn-sidebar-bg: #f6f9fc;
$twocolumn-sidebar-iconview-bg: #ffffff;

// Dark leftbar menu color
$menu-item-color-dark: #9097a7;
$menu-item-hover-color-dark: #c8cddc;
$menu-item-active-color-dark: #ffffff;

//Menu item
$menu-item: #212b35;
$menu-item-hover: #00acc1;
$menu-item-active: #0000ff;
$menu-sub-item-active: #00acc1;
$menu-item-size: 14px;
$menu-sub-item-size: 0.875rem;

// Horizontal Menu Item
$hori-menu-item-color: #6e7488;

//Rightbar Width
$rightbar-width: 260px;
$rightbar-bg: #ffffff;

//Topbar Background
$bg-topbar-light: #ffffff;
$bg-topbar-dark: #38414a;

// Leftbar item shadow (Condensed item)
$box-shadow-condensed: 3px 5px 10px 0 rgba(154, 161, 171, 0.2);

// Topnav Height (Horizontal)
$topnav-height: 55px;

// Topbar Height
$topbar-height: 80px;
$topbar-height-mobile: 60px;

// Horizontal Layout
$horizontal-layout-width: 90%;

// Body minimum height
$body-min-height: 1750px;

// Font weight
$font-weight-regular: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

// Dropdown Large
$dropdown-lg-width: 320px;

// Logo Display
$logo-dark-display: none;
$logo-light-display: block;

// Logo Display - Auth Pages
$logo-auth-dark-display: block;
$logo-auth-light-display: none;

// User authentication Background
$auth-bg: #ffffff;
$auth-bg-alt: #6658dd;
$auth-img-bg: url('../images/bg-auth.jpg');
$auth-img-pattern-bg: url('../images/bg-pattern.png');

// Apex chart
$apex-grid-color: #f9f9fd;

// Chat widget
$chat-primary-user-bg: #fef5e4;
$chat-secondary-user-bg: #f1f3fa;

// Text-color (h1 to h6)
$text-color-h: #343a40;

// Card overlay
$card-overlay-color: #3e4852;
$card-overlay-bg: rgba(#ffffff, 0.8);

// Input light gray
$input-light-bg: #f3f7f9;

// Shadow
$shadow-sm: 0 0.75rem 6rem rgba(56, 65, 74, 0.03);
$shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
$shadow-lg: 0 0 45px 0 rgba(0, 0, 0, 0.12);

// Components Shadows
$components-shadow-sm: none;
$components-shadow: none;
$components-shadow-lg: none;

// label
$label-font-weight: 500;

// Table Head
$table-head-font-weight: 700;

//
// Color system
//

$white: #fff;
$gray-100: #f3f7f9;
$gray-200: #f7f7f7;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #98a6ad;
$gray-700: #6c757d;
$gray-800: #343a40;
$gray-900: #323a46;

$black: #000;

$grays: ();
// stylelint-disable-next-line scss/dollar-variable-default
$grays: map-merge(
  (
    '100': $gray-100,
    '200': $gray-200,
    '300': $gray-300,
    '400': $gray-400,
    '500': $gray-500,
    '600': $gray-600,
    '700': $gray-700,
    '800': $gray-800,
    '900': $gray-900,
  ),
  $grays
);

$blue: #4a81d4;
$indigo: #675aa9;
$purple: #6658dd;
$pink: #f672a7;
$red: #f1556c;
$orange: #fd7e14;
$yellow: #f7b84b;
$green: #1abc9c;
$teal: #02a8b5;
$cyan: #4fc6e1;

$colors: ();
// stylelint-disable-next-line scss/dollar-variable-default
$colors: map-merge(
  (
    'blue': $blue,
    'indigo': $indigo,
    'purple': $purple,
    'pink': $pink,
    'red': $red,
    'orange': $orange,
    'yellow': $yellow,
    'green': $green,
    'teal': $teal,
    'cyan': $cyan,
    'white': $white,
    'gray': $gray-600,
    'gray-dark': $gray-800,
  ),
  $colors
);

$primary: $purple;
$secondary: $gray-700;
$success: $green;
$info: $cyan;
$warning: $yellow;
$danger: $red;
$blue: $blue;
$light: $gray-100;
$dark: $gray-900;

$theme-colors: ();
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(
  (
    'primary': $primary,
    'secondary': $secondary,
    'success': $success,
    'info': $info,
    'warning': $warning,
    'danger': $danger,
    'light': $light,
    'dark': $dark,
    'pink': $pink,
    'blue': $blue,
  ),
  $theme-colors
);

// Set a specific jump point for requesting color jumps
$theme-color-interval: 8%;

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold: 200;

// Customize the light and dark text colors for use in our YIQ color contrast function.
$yiq-text-dark: $gray-800;
$yiq-text-light: $white;

// Characters which are escaped by the escape-svg function
$escaped-characters: (('<', '%3c'), ('>', '%3e'), ('#', '%23'));

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret: false;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-prefers-reduced-motion-media-query: true;
$enable-hover-media-query: false; // Deprecated, no longer affects any compiled CSS
$enable-grid-classes: true;
$enable-pointer-cursor-for-buttons: true;
$enable-print-styles: true;
$enable-responsive-font-sizes: false;
$enable-validation-icons: true;
$enable-deprecation-messages: true;

