.diriq-table {
  color: #212b35;
  font-size: 12px;

  ._my-lists-table & {
    table-layout: fixed !important;
  }

  th {
    &:focus {
      outline: none;
    }
  }

  thead th {
    background: #dae8f7;
    font-weight: 600 !important;

    &:nth-child(2) {
      ._list-name-table &:after {
        background: #fff !important;
      }
    }
  }

  tfoot th {
    background: #f6f9fc;
    font-size: 0;
  }

  tfoot tr {
    height: 46px;
  }

  th {
    ._list-name-table.with-scroll:not(.dataTables_wrapper--empty)
      &:nth-child(2) {
      &:before {
        content: '';
        position: absolute;
        right: 0;
        bottom: -10px;
        height: 10px;
        width: 1px;
        background: #dae8f7;
      }
    }
  }

  th,
  td {
    font-weight: 500;
    // padding-left: 12px !important;
    // padding-right: 12px !important;

    ._list-name-table & {
      /*min-width: 90px;*/
      padding-left: 27px !important;
      padding-right: 13px !important;
    }

    &:first-child {
      text-align: center;
      padding-left: 29px !important;
      padding-right: 10px !important;
      min-width: auto;
      width: 12px !important;
    }

    ._my-lists-table &:last-child,
    ._list-name-table &:last-child {
      padding-left: 29px !important;
      padding-right: 29px !important;
      min-width: auto;
    }

    ._list-name-table.new &:first-child {
      vertical-align: middle !important;
    }
    ._list-name-table &:nth-child(2) {
      padding-left: 18px !important;
      padding-right: 28px !important;
      position: relative;
    }
    ._list-name-table.with-scroll &:nth-child(2) {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        width: 1px;
        background: #dae8f7;
      }
    }

    ._my-lists-table &:nth-child(2) {
      width: 180px !important;
    }

    ._report-opens-table &:nth-child(2) {
      width: 250px !important;
    }

    ._my-lists-table &:nth-child(3),
    ._my-lists-table &:nth-child(4),
    ._my-lists-table &:nth-child(5),
    ._my-lists-table &:nth-child(6),
    ._my-lists-table &:nth-child(7) {
      text-align: right;
      min-width: 72px !important;
    }
  }

  tbody tr {
    height: 40px !important;
    ._my-lists-table & {
      height: 75px !important;
      &._report-opens-table {
        height: 50px !important;
      }
    }

    transition: background-color 0.25s;

    &.hover {
      background: #f6f9fc;
    }
  }

  &__wrapper {
    padding-right: 22px;
    position: relative;

    .dataTables_length label {
      margin-bottom: 0;
      font-size: 12px;
      font-weight: 700;
      color: #212b35;
    }

    &.loading {
      .dataTables_scroll {
        position: relative;
        min-height: 200px;

        &:after {
          content: '';
          position: absolute;
          top: 40px;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(#fff, 0.85);
          z-index: 2;
        }
      }

      &.custom-table {
        min-height: 200px;
        background: #fff;
      }

      .lds-ellipsis__wrap {
        opacity: 1;
        visibility: visible;
      }
    }
    &.new.loading {
      background: #fff;

      .diriq-table__filter-btn,
      .dropdown._more {
        display: none;
      }
    }
  }

  &__btn-cell {
    font-size: 0;
  }

  &__link {
    color: #212b35;
    display: inline-block;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 600;
    vertical-align: middle;

    &:hover {
      color: #212b35;
    }

    .DTFC_LeftBodyLiner &,
    .diriq-table__wrapper:not(._list-name-table) & {
      position: relative;
      z-index: 0;

      &:after {
        z-index: -1;
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        bottom: 3px;
        width: 100%;
        height: 3px;
        background: #b6ffe8;
        transition: 0.25s;
      }

      &:hover {
        &:after {
          height: 15px;
        }
      }
    }
  }

  &__bottom {
    margin-top: 12px;
  }

  &__list-size-number {
    font-size: 24px;
    line-height: 1;
    top: -2px;
    position: relative;
    font-weight: 500;
  }

  &__list-size-percents {
    font-size: 12px;
    line-height: 1;
    color: #454f5b;
    font-weight: 500;

    span {
      font-weight: 700;
      color: #212b35;
    }
  }

  &__cell-title {
    font-size: 12px;
    font-weight: 600;
    color: #212b35;
    display: inline-block;
    position: relative;
    z-index: 1;

    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: 2px;
      left: 0;
      right: 0;
      width: 100%;
      height: 4px;
      background: #b6ffe8;
      z-index: -1;
    }
  }

  &.dataTable thead th.sorting,
  &.dataTable thead th.sorting_asc,
  &.dataTable thead th.sorting_desc {
    background-image: none;
  }

  .sort-i__wrap {
    margin-left: 12px;
  }

  &__filter-btn {
    width: 60px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 22px;
    cursor: pointer;
    background: #f6f9fc;

    ._filter-hidden & {
      background: #fff;
      z-index: 11;
    }
  }

  &__filter-icon,
  &__close-icon {
    transition: color 0.25s;
  }

  &__filter-icon {
    color: #00eba8;
  }

  &__close-icon {
    color: #b6ffe8;
  }

  &__filter-btn:hover &__filter-icon,
  &__filter-btn:hover &__close-icon {
    color: #0000ff;
  }

  &__close-icon-cross {
    fill: #000;
    transition: fill 0.25s;
  }

  &__filter-btn:hover &__close-icon-cross {
    fill: #fff;
  }

  .diriq-table__wrapper._filter-hidden &__close-icon,
  .diriq-table__wrapper:not(._filter-hidden) &__filter-icon {
    display: none;
  }
}

