// Datatable scrollbars
.dataTables {
  &_scrollHead {
    background: #ecf1f8;
  }

  &_scrollBody {
    color: #fff;
    transition: color 0.25s;

    &:hover {
      color: #b6ffe8;
      &::-webkit-scrollbar-track-piece {
        background-color: #b6ffe8;
      }
      &::-webkit-scrollbar-thumb {
        background: #fff;
        border-color: #b6ffe8;
        cursor: pointer;
      }
    }

    &:active {
      color: #fff;
      &::-webkit-scrollbar-track-piece {
        background-color: #fff !important;
      }
      &::-webkit-scrollbar-thumb {
        background: #0000ff;
        border-color: #fff;
      }
    }

    &::-webkit-scrollbar {
      width: 20px;
      background: #f6f9fc;
      transition: 0.5s;
      &:active {
        background: transparent;
      }
    }

    &::-webkit-scrollbar-thumb {
      background: #00eba8;
      border: 5px solid#f6f9fc;
      border-radius: 20px;
      transition: 0.5s;
      height: 70px;
      cursor: pointer;
      &:active {
        background: #0000ff;
        border-color: #fff;
      }
    }

    &::-webkit-scrollbar-track-piece {
      background-color: #f6f9fc;
    }
  }
}

.quality-icon {
  display: inline-block;
  white-space: nowrap;
  font-size: 0;
  span {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #dae8f7;

    & + span {
      margin-left: 2px;
    }
  }
  &._1 span {
    &:first-child {
      background: #b6ffe8;
    }
  }
  &._2 span {
    &:first-child,
    &:nth-child(2) {
      background: #b6ffe8;
    }
  }
  &._3 span {
    &:first-child,
    &:nth-child(2),
    &:nth-child(3) {
      background: #00eba8;
    }
  }
  &._4 span {
    &:first-child,
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
      background: #00eba8;
    }
  }
  &._5 span {
    background: #00eba8;
  }
}

.table-status {
  padding: 2px 5px;
  border: 1px solid #212b35;
  font-weight: 600;
  white-space: nowrap;

  &._success {
    color: #00eba8;
    border-color: #00eba8;
  }

  &._danger {
    color: #ed1c24;
    border-color: #ffcfef;
  }

  &._small {
